.TodoInsert{
  display: flex;
  background-color: #495057;

  input {
    // 기본 스타일 초기화
    background: none ; outline: none; border: none; padding: 0.5rem; font-size: 1.125rem; line-height: 1.5; color: #fff;

    &::placeholder{
      color: #dee2e6
    }
    // 버튼을 제외한 모든 영역 차지
    flex: 1;
  }

  button {
    background: none; outline: none; border: none; background-color: #868e96; color : #fff; padding-left: 1rem; padding-right: 1rem; font-size: 1.5rem; display: flex; align-items: center; cursor: pointer; transition: .1s background ease-in;

    &:hover {
      background-color: #adb5bd;
    }
  }
}