
.TodoListItem-virtualized {
  & + & {
    border-top: 1px solid #dee2e6;
  }
  &:nth-child(even) {
    background: #f8f9fa;
  }
}

.TodoListItem {
  padding: 1rem; display: flex; align-items: center;
  &:nth-child(even) {
    background-color: #f8f9fa;
  }

  .checkbox {
    cursor: pointer; flex: 1; // 차지할 수 있는 모든 영역 차지
    display: flex; align-items: center;
    svg {
      // 아이콘
      font-size: 1.5rem;
    }
    .text {
      margin-left: .5rem; flex: 1;
    }
    
    // 체크되어 있을 때 보여질 스타일
    &.checked {
      svg {
        color: #22b8cf;
      }
      .text {
        color: #adb5bd;
        text-decoration: line-through;
      }
    }
  }

  .remove {
    display: flex; align-items: center; font-size: 1.5rem; color: #ff6b6b; cursor: pointer;

    &:hover {
      color: #ff8787;
    }
  } // remove end

  //엘리멘트 사이사이 테두리를 넣어 줌
  & + & {
    border-top :1px solid #dee2e6;
  }
}